const getDataAndVote = function(authors){
  let acc = this.$store.state.username
  console.log(122, "",  authors)
  let key =this.$store.state.password

  let that = this
  this.steem.api.streamTransactions("head", function (err, result) {
    // console.log(234, err, result)
    if (err) {
      alert("API!")
    }
    let txType = result.operations[0][0]  //
    let txData = result.operations[0][1]  //

    authors.forEach(item => {
      if (txType == "comment" & txData.author == item.author & txData.parent_author == "") {//
        let permlink = txData.permlink
        let voteWeight = item.weight
        let vote = function (){
          that.steem.broadcast.vote( key, acc, item.author, permlink, voteWeight*100, function(err, result) {
            console.log(688, "", item.author,  txData.title)
          })
        }
        setTimeout(vote, 300000)  //5
      }
    })
  })
}

export {getDataAndVote}
